n\n.btn-group-justified .btn-item {\n    padding-left: 2px;\n    padding-right: 2px;\n}\n",".sidebar {\n    position: fixed;\n    width: 250px;\n    background-color: $color-bg-secondary;\n    height: 100%;\n    top: 0;\n    left: 0;\n    z-index: 999;\n    background-repeat: no-repeat;\n    background-position: center center;\n    background-size: cover;\n    a {\n        color: rgba(255, 255, 255, 0.7);\n        &:hover {\n            color: #fff;\n        }\n    }\n    .copyright {\n        position: absolute;\n        width: 100%;\n        bottom: 0;\n        left: 0;\n        padding: 15px 30px;\n        font-size: 13px;\n        font-weight: 300;\n        color: $color-text-primary-invert;\n    }\n    .social-icons a {\n        font-size: 12px;\n        margin-right: 10px;\n        position: relative;\n    }\n    &.nav {\n        width: 95%;\n    }\n}\n\n.nav.child_menu {\n    display: block;\n}\n\n@media screen and (max-width: 767px) {\n    .row-offcanvas {\n        position: relative;\n        -webkit-transition: all 0.25s ease-out;\n        -moz-transition: all 0.25s ease-out;\n        transition: all 0.25s ease-out;\n    }\n    .row-offcanvas-right .sidebar-offcanvas {\n        right: -41.6%;\n    }\n    .row-offcanvas-left .sidebar-offcanvas {\n        left: -41.6%;\n    }\n    .row-offcanvas-right.active {\n        right: 41.6%;\n    }\n    .row-offcanvas-left.active {\n        left: 41.6%;\n    }\n    .sidebar-offcanvas {\n        position: absolute;\n        top: 0;\n        width: 41.6%;\n    }\n    #sidebar {\n        padding-top: 0;\n    }\n}\n\n@media (max-width: 991px) {\n    .sidebar {\n        position: relative;\n        z-index: 99999;\n        height: auto;\n        width: 100%;\n        padding: 0;\n        &:after {\n            content: normal;\n        }\n        .copyright {\n            display: none;\n        }\n    }\n    .wrapper {\n        margin-left: 0;\n    }\n}\n","/*!------------------------------------------------------------------\n[Nice Checkboxes and Radio Buttons]\n*/\n\n.control {\n    display: block;\n    position: relative;\n    padding-left: 30px;\n    margin-bottom: 16px;\n    cursor: pointer;\n    font-size: 18px;\n    input {\n        position: absolute;\n        z-index: -1;\n        opacity: 0;\n    }\n}\n\n.control__indicator {\n    position: absolute;\n    top: 2px;\n    left: 0;\n    height: 20px;\n    width: 20px;\n    background: #e6e6e6;\n}\n\n.control--radio .control__indicator {\n    border-radius: 50%;\n}\n\n.control {\n    &:hover input ~ .control__indicator {\n        background: #ccc;\n    }\n    input {\n        &:focus ~ .control__indicator {\n            background: #ccc;\n        }\n        &:checked ~ .control__indicator {\n            background: #111111;\n        }\n    }\n    &:hover input:not([disabled]):checked ~ .control__indicator {\n        background: #111111;\n    }\n    input {\n        &:checked:focus ~ .control__indicator {\n            background: #111111;\n        }\n        &:disabled ~ .control__indicator {\n            background: #e6e6e6;\n            opacity: 0.6;\n            pointer-events: none;\n        }\n    }\n}\n\n.control__indicator:after {\n    content: \"\";\n    position: absolute;\n    display: none;\n}\n\n.control input:checked ~ .control__indicator:after {\n    display: block;\n}\n\n.control--checkbox {\n    .control__indicator:after {\n        left: 8px;\n        top: 4px;\n        width: 3px;\n        height: 8px;\n        border: solid #fff;\n        border-width: 0 2px 2px 0;\n        transform: rotate(45deg);\n    }\n    input:disabled ~ .control__indicator:after {\n        border-color: #7b7b7b;\n    }\n}\n\n.control--radio {\n    .control__indicator:after {\n        left: 7px;\n        top: 7px;\n        height: 6px;\n        width: 6px;\n        border-radius: 50%;\n        background: #fff;\n    }\n    input:disabled ~ .control__indicator:after {\n        background: #7b7b7b;\n    }\n}\n","/*!------------------------------------------------------------------\n[Preloader]\n*/\n.page-loader {\n    background: #fff;\n    position: fixed;\n    top: 0;\n    bottom: 0;\n    right: 0;\n    left: 0;\n    z-index: 9998;\n}\n\n.loader {\n    position: absolute;\n    width: 50px;\n    height: 50px;\n    top: 50%;\n    left: 50%;\n    margin: -25px 0 0 -25px;\n    text-indent: -9999em;\n    background-color: transparent;\n    border: 5px solid #eceff1;\n    -webkit-border-radius: 50%;\n    -moz-border-radius: 50%;\n    -o-border-radius: 50%;\n    border-radius: 50%;\n}\n\n.loader:after {\n    position: absolute;\n    content: \"\";\n    top: 0px;\n    left: 0px;\n    width: 40px;\n    height: 40px;\n    padding: 2px;\n    border-width: 2px;\n    border-style: solid;\n    border-color: #111 transparent transparent transparent;\n    -webkit-border-radius: 50%;\n    -moz-border-radius: 50%;\n    -o-border-radius: 50%;\n    border-radius: 50%;\n    box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n    animation: ringrotate 0.8s infinite cubic-bezier(0.5, 0.49, 0.49, 0.49);\n    -webkit-animation: ringrotate 0.8s infinite\n        cubic-bezier(0.5, 0.49, 0.49, 0.49);\n}\n\n@keyframes ringrotate {\n    0% {\n        -webkit-transform: rotateZ(0deg);\n        -moz-transform: rotateZ(0deg);\n        -o-transform: rotateZ(0deg);\n        transform: rotateZ(0deg);\n    }\n    100% {\n        -webkit-transform: rotateZ(360deg);\n        -moz-transform: rotateZ(360deg);\n        -o-transform: rotateZ(360deg);\n        transform: rotateZ(360deg);\n    }\n}\n\n@-webkit-keyframes ringrotate {\n    0% {\n        -webkit-transform: rotateZ(0deg);\n        -moz-transform: rotateZ(0deg);\n        -o-transform: rotateZ(0deg);\n        transform: rotateZ(0deg);\n    }\n    100% {\n        -webkit-transform: rotateZ(360deg);\n        -moz-transform: rotateZ(360deg);\n        -o-transform: rotateZ(360deg);\n        transform: rotateZ(360deg);\n    }\n}\n","/*!------------------------------------------------------------------\n[Switch]\n*/\n\n.switch-block {\n    display: flex;\n    align-items: center;\n    .switch {\n        margin: 0 20px;\n    }\n    .switch-label {\n        text-transform: uppercase;\n        letter-spacing: 1px;\n        font-size: 12px;\n        font-weight: 500;\n        color: $color-text-primary-light;\n        transition: 0.2s color linear;\n    }\n    &.on .switch-on-text {\n        color: $color-text-secondary;\n    }\n    &.off .switch-off-text {\n        color: $color-text-secondary;\n    }\n}\n\n/*!------------------------------------------------------------------\n[Sliders]\n*/\n\n.switch {\n    position: relative;\n    display: inline-block;\n    width: 30px;\n    height: 12px;\n\n    .slider {\n        position: absolute;\n        cursor: pointer;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background-color: #ccc;\n        transition: 0.4s;\n        &:before {\n            position: absolute;\n            content: \"\";\n            height: 20px;\n            width: 20px;\n            left: -6px;\n            bottom: -3px;\n            background-color: white;\n            -webkit-transition: 0.4s;\n            transition: 0.4s;\n            box-shadow: 0 2px 8px rgba(0, 0, 0, 0.28);\n        }\n        &.round {\n            border-radius: 34px;\n            &:before {\n                border-radius: 50%;\n            }\n        }\n    }\n\n    input {\n        display: none;\n\n        &:checked + .slider {\n            background-color: $color-accent;\n        }\n        &:focus + .slider {\n            box-shadow: 0 0 1px $color-accent;\n        }\n        &:checked + .slider:before {\n            -webkit-transform: translateX(26px);\n            -ms-transform: translateX(26px);\n            transform: translateX(26px);\n        }\n    }\n}\n\n@media (max-width: 991px) {\n    .slides-container .hero-caption {\n        padding-right: 0;\n    }\n}\n","/*!------------------------------------------------------------------\n[Navbar]\n*/\n\n.navbar-brand {\n    font-weight: 100;\n    font-size: 23px;\n    text-transform: uppercase;\n    letter-spacing: 2px;\n}\n.navbar-custom {\n    background: transparent;\n    border: 0;\n    padding: 0;\n    margin: 0;\n    .navbar-brand {\n        display: table-cell;\n        vertical-align: middle;\n        float: none;\n        height: 70px;\n        padding-top: 0;\n        padding-bottom: 0;\n        color: $color-text-primary-invert;\n        width: 100vw;\n        text-shadow: 1px 1px 0 #000;\n        small {\n            display: block;\n            font-size: 12px;\n            font-family: \"Source Sans Pro\", sans-serif;\n        }\n    }\n    li > a,\n    .dropdown-menu li > a {\n        color: $color-menu-link;\n    }\n    .nav {\n        > li > a {\n            &:hover {\n                background: transparent;\n                color: #fff;\n            }\n            &:focus {\n                background: transparent;\n                border-color: transparent;\n            }\n        }\n        .open > a {\n            background: transparent;\n            &:hover {\n                background: transparent;\n                color: #fff;\n            }\n            &:focus {\n                background: transparent;\n            }\n        }\n    }\n    .dropdown-menu {\n        > li > a {\n            &:hover,\n            &:focus {\n                background: transparent;\n                color: $color-accent;\n            }\n        }\n        background: rgba(26, 26, 26, 0.9);\n        border: 0;\n        padding: 5px 0;\n        -webkit-box-shadow: none;\n        -moz-box-shadow: none;\n        -o-box-shadow: none;\n        box-shadow: none;\n        -webkit-border-radius: 0;\n        -moz-border-radius: 0;\n        -o-border-radius: 0;\n        border-radius: 0;\n    }\n    .navbar-toggle {\n        .icon-bar {\n            background: #fff;\n        }\n        padding: 0;\n        margin: 27px 15px 0 0;\n    }\n    .navbar-nav {\n        margin-top: 0;\n        margin-bottom: 0;\n        padding: 0px 0 0px 20px;\n    }\n    .navbar-collapse {\n        -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        -moz-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        -o-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n    }\n    .navbar-nav {\n        i {\n            width: 12px;\n        }\n        .menu-icon {\n            margin-left: -20px;\n            margin-top: 6px;\n            font-size: 12px;\n            color: $color-accent;\n        }\n        > li.item a {\n            margin-left: 10px;\n        }\n        ul {\n            list-style: none;\n        }\n        .parent {\n            padding-bottom: 4px;\n        }\n        // li ul li {\n        //     //padding: 6px 0px;\n        // }\n    }\n}\n\n@media (min-width: 992px) {\n    .navbar-custom {\n        .navbar-header {\n            float: none;\n            margin: 0 0 10px;\n            padding: 20px 20px;\n        }\n        .navbar-nav {\n            float: none;\n            li {\n                float: none;\n            }\n        }\n        li > a,\n        .dropdown-menu li > a {\n            padding-top: 12px;\n            padding-bottom: 12px;\n        }\n        li.dropdown > a:after {\n            position: absolute;\n            display: block;\n            right: 35px;\n            top: 50%;\n            margin-top: -6px;\n            font: normal normal normal 14px/1 FontAwesome;\n            font-size: 10px;\n            content: \"\\f105\";\n            text-rendering: auto;\n            -webkit-font-smoothing: antialiased;\n            -moz-osx-font-smoothing: grayscale;\n        }\n        .dropdown-menu {\n            top: -5px;\n            left: 100%;\n            min-width: 200px;\n        }\n        .navbar-collapse {\n            padding-left: 0;\n            padding-right: 0;\n            box-shadow: none;\n        }\n    }\n}\n\n@media (max-width: 991px) {\n    .navbar-custom li > a {\n        -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        -moz-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        -o-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);\n    }\n    .navbar-custom .navbar-nav .open .dropdown-menu > li > a,\n    .navbar-custom .navbar-nav .open .dropdown-menu .dropdown-header {\n        padding: 10px 15px 10px 25px;\n    }\n    .navbar-nav > li > .dropdown-menu {\n        padding-top: 0;\n        padding-bottom: 0;\n    }\n}\n","/*!------------------------------------------------------------------\n[Post]\n*/\n.post-thumbnail img {\n    width: 100%;\n    max-width: none;\n}\n.post {\n    margin-bottom: 20px;\n    h1:first-child,\n    h2:first-child,\n    h3:first-child,\n    h4:first-child,\n    h5:first-child,\n    h6:first-child {\n        margin-top: 0px;\n    }\n    p > img {\n        width: 100%;\n        max-width: none;\n    }\n    .post-header {\n        margin: 16px 0 16px;\n        .post-meta {\n            color: $color-text-muted;\n        }\n    }\n\n    .post-content {\n        padding-bottom: 20px;\n        padding: 0px;\n    }\n\n    .post-title {\n        font-weight: 500;\n        min-height: 20px;\n        margin: 0 0 10px;\n        text-transform: initial;\n        a {\n            font-weight: 500;\n            color: $color-text-primary;\n            margin: 0 0 10px;\n        }\n    }\n    a.post-more {\n        background: $color-text-secondary;\n        color: $color-border;\n        border: 1px solid $color-text-secondary;\n        padding: 6px 10px;\n        transition: all 0.2s linear;\n        text-transform: uppercase;\n        letter-spacing: 2px;\n        font-size: 11px;\n        &:hover {\n            background: $color-border;\n            color: $color-text-secondary;\n            border: 1px solid $color-text-secondary;\n        }\n    }\n    .slider {\n        img {\n            margin-left: 0;\n            margin-right: 0;\n        }\n        &.owl-carousel {\n            width: 120%;\n            margin-left: -10%;\n            margin-right: -10%;\n        }\n    }\n    .fluid-width-video-wrapper {\n        width: 120%;\n        margin-left: -10%;\n        margin-right: -10%;\n    }\n    .gallery {\n        margin-left: -10%;\n        margin-right: -10%;\n    }\n    blockquote {\n        margin: 60px -10%;\n    }\n}\n\n@media (max-width: 991px) {\n    .post p > img,\n    .post-thumbnail img {\n        max-width: 100%;\n        margin-left: 0;\n        margin-right: 0;\n    }\n    .post {\n        .slider.owl-carousel,\n        .fluid-width-video-wrapper {\n            max-width: 100%;\n            margin-left: 0;\n            margin-right: 0;\n        }\n        pre,\n        .gallery,\n        blockquote {\n            margin-left: 0;\n            margin-right: 0;\n        }\n    }\n}\n","/*!------------------------------------------------------------------\n[Modal Windows]\n*/\n.full-width-modal {\n    .modal-dialog {\n        width: 100%;\n        height: 100%;\n        margin: 0;\n        padding: 0;\n\n        .modal-content {\n            height: auto;\n            min-height: 100%;\n            border-radius: 0;\n            .modal-body {\n                height: calc(100vh - 110px);\n                overflow: scroll;\n            }\n        }\n    }\n}\n",".login-view {\n    background: url(\"/images/login_bg.jpg\");\n    color: #dcdcdc;\n    background-repeat: no-repeat;\n    background-size: cover;\n    .login-wrapper {\n        display: flex;\n        justify-content: space-around;\n        align-items: center;\n        height: 100vh;\n\n        .brand {\n            text-align: center;\n            font-weight: 100;\n            font-size: 40px;\n            color: #f0e8e9;\n            text-shadow: -1px 1px 27px #472532;\n            font-family: \"Source Sans Pro\", sans-serif;\n        }\n        .login {\n            width: 100%;\n            max-width: 400px;\n            padding: 40px 20px;\n            form {\n                input {\n                    text-transform: none;\n                    border: 1px solid #eee;\n                    color: #5a5b5c;\n                    font-size: 13px;\n                }\n                input:-webkit-autofill {\n                    -webkit-box-shadow: 0 0 0px 1000px #f5f5f5 inset;\n                }\n                label {\n                    display: inline-block;\n                    max-width: 100%;\n                    margin-bottom: 5px;\n                    font-weight: bold;\n                    letter-spacing: 4px;\n                    font-weight: 100;\n                    text-transform: uppercase;\n                    font-size: 11px;\n                    margin-top: 19px;\n                }\n                .form-signin-heading {\n                    margin-bottom: 18px;\n                    font-size: 30px;\n                    color: #5a5b5c;\n                }\n                .form-control {\n                    position: relative;\n                    height: auto;\n                    padding: 9px 8px;\n                    box-sizing: border-box;\n                    border: 1px solid #fff;\n                    background: #fff;\n                    &:focus {\n                        z-index: 2;\n                    }\n                }\n            }\n            a.forgot-pwd {\n                color: #d7c8c8;\n            }\n        }\n    }\n}\n","/* Tell Quill not to scroll */\n\n#editor {\n    height: auto;\n    min-height: 300px;\n    padding: 0px;\n    border: none;\n    font-family: inherit;\n\n    .ql-editor {\n        overflow-y: visible;\n        padding: 12px 0px;\n        min-height: 100vh;\n        padding-bottom: 400px;\n        line-height: 1.7;\n    }\n}\n\n.ql-container {\n    font-size: 16px;\n}\n\n/* Specify our own scrolling container */\n\n#quill-container {\n    height: 100%;\n    min-height: 100%;\n    overflow-y: auto;\n\n    .ql-toolbar {\n        opacity: 1;\n        position: fixed;\n        bottom: 0px;\n        left: 250px;\n        background: #fff;\n        z-index: 9;\n        width: calc(100% - 250px);\n        text-align: center;\n    }\n}\n.options-open {\n    .ql-toolbar {\n        text-align: left !important;\n    }\n}\n@media screen and (max-width: 991px) {\n    .ql-toolbar {\n        width: 100%;\n    }\n}\n","/*Media*/\n\n.table.media-table {\n    td {\n        vertical-align: middle;\n    }\n    .media-image {\n        object-fit: cover;\n        width: 80px;\n        height: 80px;\n    }\n}\n\n.modal {\n    .post {\n        border: 4px solid transparent;\n        cursor: pointer;\n        &:hover {\n            border: 4px solid #d5d5d6;\n        }\n    }\n    .post.selected {\n        border: 4px solid #d5d5d6;\n    }\n}\n.media-grid .post {\n    .post-header {\n        margin: 0px;\n        font-size: 12px;\n    }\n    .post-time {\n        font-size: 11px;\n    }\n    .post-body {\n        padding: 4px 8px;\n        border: 1px solid #eee;\n        .post-content {\n            display: flex;\n            justify-content: space-between;\n        }\n    }\n}\n",".navbar-custom {\n    .nav-list li a {\n        text-decoration: none;\n        display: block;\n        padding: 10px;\n        cursor: pointer;\n        border-bottom: 1px solid #2d2d2d !important;\n        color: $color-menu-link;\n        border-left: 4px solid transparent;\n    }\n    .nav-list li.active a {\n        border-left: 4px solid $color-accent;\n    }\n    .nav-list > li > a {\n        color: $color-menu-link;\n        font-size: 14px;\n        padding-left: 13px !important;\n        border-bottom: 1px solid #2e2e2e;\n    }\n    .nav-list > li a:hover {\n        border-left: 4px solid $color-accent;\n    }\n    .nav-list a span {\n        margin-left: 20px;\n    }\n    .nav-list .menu-icon {\n        margin-top: 4px;\n    }\n    .nav-list li.has-sub li a {\n        padding-left: 34px !important;\n    }\n    #menutree {\n        max-height: calc(100vh - 210px);\n        overflow: scroll;\n    }\n}\n",".distract-free {\n    .distractor {\n        display: none !important;\n    }\n    .wrapper {\n        margin: 0px;\n    }\n    .article-holder {\n        position: absolute;\n        left: 50%;\n        width: 1000px;\n        margin-left: -500px;\n    }\n    .top-bar {\n        width: 100%;\n    }\n    @media screen and (max-width: 1000px) {\n        .article-holder {\n            width: 100%;\n            left: 0px;\n            margin: 0px;\n            padding: 0px;\n        }\n    }\n}\n\n/*--------------------------------------\n[Edit/Create Post or Page]\n*/\n.post-title {\n    margin: 0px;\n}\n.post-excerpt {\n    display: block;\n    min-height: 80px;\n}\n#md-post,\n#md-preview {\n    min-height: 100vh;\n    margin-top: 16px;\n}\n.featured-image {\n    img {\n        object-fit: cover;\n        height: 100%;\n    }\n    .btn {\n        margin-top: -48px;\n    }\n    height: 200px;\n}\n.file-explorer .post-thumbnail img {\n    height: 100px;\n    object-fit: cover;\n    overflow: hidden;\n}\n\n.action-bar {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: center;\n}\n.article-holder {\n    .post-header {\n        display: flex;\n        justify-content: space-between;\n    }\n}\n\n.post-grid .post {\n    margin-bottom: 0px;\n    box-shadow: -2px 2px 14px -2px #ccc;\n}\n\n/*!------------------------------------------------------------------\n[Post Options]\n*/\n\n.edit-post-page,\n.create-post-page {\n    .column.distractor {\n        display: none;\n    }\n    &.options-open {\n        .column.distractor {\n            display: block;\n        }\n    }\n    &.distract-free {\n        .post-options {\n            display: none !important;\n        }\n    }\n    .post-options,\n    .full-screen {\n        display: block !important;\n    }\n}\n\n/*!------------------------------------------------------------------\n[Post Filters]\n*/\n\n.post-filters {\n    text-transform: uppercase;\n    font-size: 12px;\n    color: #afafaf;\n    .filter-item:not(:last-child):after {\n        content: \"|\";\n        margin: 0px 10px;\n        color: #afafaf;\n    }\n    .filter-item {\n        transition: 0.3s all linear;\n        cursor: pointer;\n        &.active {\n            font-weight: 600;\n            color: $color-text-secondary;\n        }\n    }\n}\n","/*--------------------------------------------\n[Menu Construction]\n*/\n.listing i.fa {\n    margin-right: 8px;\n    color: $color-text-primary-light;\n}\n\n/*--------------------------------------------\n[Menu Construction]\n*/\n\n.nestable-item-name {\n    .panel-heading {\n        background: #eee;\n        border: 1px solid #ddd;\n        padding: 12px 16px;\n        cursor: pointer;\n    }\n    .panel-title {\n        font-size: $fs-small;\n        text-transform: capitalize;\n    }\n    .panel-collapse {\n        border: 1px solid #eee;\n        border-top: none;\n    }\n}\n\n.nestable-item-handler {\n    &:before {\n        content: \"\\F0C9\";\n        font-family: FontAwesome;\n        font-style: normal;\n        font-weight: normal;\n        text-decoration: inherit;\n        color: #000;\n        padding-right: 0.5em;\n        cursor: move;\n    }\n}\n\n.menu-construct {\n    h5 {\n        margin-bottom: 8px;\n        font-size: 12px;\n    }\n    .resources {\n        &.list-group {\n            border: 1px solid #e6e5e5;\n            max-height: 200px;\n            overflow-y: scroll;\n            box-shadow: 0 0 10px 0 #f0f0f0;\n            width: 300px;\n            .list-group-item {\n                border-left: none;\n                border-right: none;\n                cursor: pointer;\n                &[disabled] {\n                    background: #eee;\n                    cursor: not-allowed;\n                    color: #919191;\n                }\n                &:first-child {\n                    border-top: none;\n                }\n                &:last-child {\n                    border-bottom: none;\n                }\n            }\n        }\n    }\n}\n","/* Tags Input */\n.ReactTags__tags {\n    position: relative;\n}\n.ReactTags__tagInput {\n    width: 200px;\n    border-radius: 2px;\n    display: block;\n    input {\n        margin: 0;\n        width: 100%;\n        border: none;\n        padding: 8px 0px 10px;\n        display: block;\n        &:focus {\n            margin: 0;\n            width: 100%;\n            border: none;\n            padding: 8px 0px 10px;\n            display: block;\n        }\n    }\n}\n.ReactTags__selected {\n    span.ReactTags__tag {\n        border: 1px solid #ddd;\n        background: #eee;\n        font-size: 12px;\n        display: inline-block;\n        padding: 0px 8px;\n        margin-right: 4px;\n        cursor: move;\n        border-radius: 16px;\n    }\n    a.ReactTags__remove {\n        color: #aaa;\n        margin-left: 5px;\n        cursor: pointer;\n        font-size: 14px;\n    }\n}\n.ReactTags__suggestions {\n    position: absolute;\n    z-index: 10;\n    ul {\n        list-style-type: none;\n        box-shadow: 0.05em 0.01em 0.5em rgba(0, 0, 0, 0.2);\n        background: white;\n        width: 220px;\n        padding: 0;\n        margin: 0;\n    }\n    li {\n        border-bottom: 1px solid #ddd;\n        padding: 8px 4px;\n        margin: 0;\n        mark {\n            padding: 0 0 0 0.4em;\n            background: transparent;\n        }\n    }\n}\n\nli.ReactTags__activeSuggestion {\n    background: #eaeaea;\n    cursor: pointer;\n}\n",".edit-author-page {\n    .avatar-wrapper {\n        display: flex;\n        align-items: center;\n        margin-top: 8px;\n        .avatar-image {\n            width: 60px;\n            height: 60px;\n            margin-right: 16px;\